<template>
    <section>
        <t-title :title="title"></t-title>
        <ul class="goods">
            <group-list v-for="(i,index) in list" :item="i" jumpTo="groupProduct" :key="index"></group-list>
        </ul>
    </section>
</template>

<script>
    import tTitle from '../../index/home/top-title.vue'
    import groupList from '../../common/list/groupList.vue'
    export default {
        props: {
            list: {
                type: Array,
                required: true,
                default() {
                    return []
                },
            },
            title:{
                type: String
            }
        },
        mounted () {

        },
        components: {
            tTitle,
            groupList
        }
    }
</script>
<style lang="less" scoped>
    @import '../../../assets/less/common.less';
   /* ul{
        margin: 0;
        padding: 0;
        background: #fff;
        display: flex;
        flex-wrap: wrap;
    }*/
    .goods {
        width: 100%;
    }
</style>



